.pop-speaker {
	position: relative;
	border: 15px solid #5f5f5f;
	background: #131513;
	width: 100%;
	z-index: 100;
	display: block;
	--sideSpacing: 50px;
	@include regular;
	@include sm {
		--sideSpacing: 30px; }

	&.fancybox-content {
		padding: 0; }

	&__top {
		position: relative;
		display: flex;
		padding: var(--sideSpacing);
		background: url(../img/bg-heads.png) center top;

		@include xs {
			flex-direction: column; } }

	&__avatar-wrap {
		flex: 0 0 auto;
		position: relative;
		width: 185px;
		height: 185px;
		margin: 0 var(--sideSpacing) 0 0;

		@include xs {
			margin: 0 auto; } }

	&__avatar {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		object-fit: cover; }


	&__desc {

		@include xs {
			text-align: center; } }

	&__name {
		@include regular;
		font-size: 30px;
		margin: -4px 0 10px;
		color: $white;

		@include xs {
			margin: 20px 0 10px; } }

	&__company {
		font-size: 16px;
		margin: 0 0 20px;
		color: $white; }

	&__bio {
		@include medium;
		font-size: 14px;
		color: #5f5f5f;
		line-height: 1.46;

		p {
			&:not(:first-child) {
				margin: 16px 0 0; } } }

	&__mid {
		padding: var(--sideSpacing) calc(254px + var(--sideSpacing)) var(--sideSpacing) var(--sideSpacing);

		@include xs {
			padding: var(--sideSpacing); } }


	&__activity-info {
		display: flex;
		border-left: 3px solid #fff;
		background: #222522;
		padding: 17px 15px;
		margin: 0 0 30px;
		flex-wrap: wrap;

		&:not(:first-child) {
			margin: 24px 0 0; }

		span {
			margin: 0 15px;
			padding: 0;
			white-space: nowrap;

			&:first-child {
				margin: 0 auto 0 0; } } }

	&__activity-title {
		@include regular;
		font-size: 28px;
		margin: -4px 0 10px;
		color: $white; }

	&__activity-desc {
		@include medium;
		font-size: 14px;
		color: #5f5f5f;
		line-height: 1.46;

		p {
			&:not(:first-child) {
				margin: 16px 0 0; } } }

	&__btn {
		width: auto;
		margin: 24px auto 0 0; }

	&__socials {
		width: 224px;
		position: absolute;
		right: var(--sideSpacing);
		top: calc(100% + var(--sideSpacing));

		@include xs {
			position: relative;
			right: auto;
			margin: 30px auto 0; }

		.btn {

			&:not(:first-child) {
				margin: 10px 0 0; } }

		.icon {
			margin: 0 10px 0 0;
			width: 16px;
			height: 16px;
			fill: #a3e200;
			transition: all ease 0.1s; } } }
