.live-programm {
	background: $black;

	&__tab-links {
		display: flex;
		padding: 0;
		letter-spacing: normal;
		border-bottom: 1px solid #161817;
		margin: 0 0 50px;
		overflow-x: auto;

		@include md {
			margin: 0 0 20px; }
		@include xs {
			margin: 0; }

		&::-webkit-scrollbar {
			width: 4px;
			height: 4px; }

		&::-webkit-scrollbar-button {
			width: 0px;
			height: 0px; }

		&::-webkit-scrollbar-thumb {
			background: #090a09;
			border: 0px none #ffffff;
			border-radius: 0; }

		&::-webkit-scrollbar-thumb:hover {
			background: #666666; }

		&::-webkit-scrollbar-thumb:active {
			background: #666666; }

		&::-webkit-scrollbar-track {
			background: #212424;
			border: 0px none #ffffff;
			border-radius: 50px; }

		&::-webkit-scrollbar-track:hover {
			background: #ffffff; }

		&::-webkit-scrollbar-track:active {
			background: #ffffff; }

		&::-webkit-scrollbar-corner {
			background: transparent; }

		.container {
			width: 100%;
			display: flex;

			@include md {
				padding: 0; } } }

	.container {
		@include xs {
			padding: 0; } }


	&__tab {
		position: relative;
		align-items: center;
		justify-content: center;
		display: flex;
		height: 60px;
		padding: 0 42px;
		text-decoration: none;
		color: $white;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 20px;
		cursor: pointer;
		transition: all ease 0.1s;

		@include tablet {
			padding: 0 20px;
			font-size: 16px; }

		@include sm {
			font-size: 14px; }

		&:hover {
			color: $themeColor; }


		.icon {
			fill: #5f5f5f;
			width: 14px;
			height: 14px;
			position: relative;
			top: 0px;
			margin: 0 5px 0 0;
			transition: all ease 0.1s; }


		&.is-active {
			background: #181a18;

			&:after {
				opacity: 1; } }


		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 4px;
			background: $themeColor;
			opacity: 0;
			transition: all ease 0.1s; } }

	&__tab-link {
		text-transform: none;
		font-size: 14px;
		text-decoration: none;
		font-weight: 500;
		color: #5f5f5f;
		margin: 0 0 0 40px;
		height: 100%;
		display: flex;
		align-items: center;
		transition: all ease 0.1s;

		span {
			margin: 0 0 0 5px; }

		@include tablet {
			margin: 0 0 0 20px; }

		@include md {
			white-space: nowrap;

			span {
				display: none; } }

		&:hover {
			color: $themeColor;

			.icon {
				fill: $themeColor; } } } }

.tab-container {
	display: none;
	background: #1e1e1e;
	margin: 0 0 40px;

	&.is-active {
		display: block; }

	@include xs {
		margin: 0; } }

.broadcast {
	width: 100%;
	padding-bottom: 56%; }
