.s-sponsors-offers {
	padding: 120px 0 120px;
	@include md {
		padding: 80px 0 80px; }
	@include sm {
		padding: 60px 0 60px; } }

.sponsors-offers {
	display: flex;
	padding: 0;
	margin: 68px auto 0;
	max-width: 1425px;
	@include sm {
		flex-direction: column;
		max-width: 480px; }

	&__item {
		border: 4px solid #2e2e2c;
		background: #191917;
		padding: 0 40px 60px;
		margin: 0 15px;
		flex: 1 1;
		display: flex;
		flex-direction: column;
		@include md {
			margin: 0 0 0 -4px;
			padding: 0 30px 30px; }
		@include sm {
			margin: -4px 0 0 0;
			padding: 0 20px 20px; } }

	&__img-wrap {
		height: 184px;
		display: flex;
		padding: 0 0;
		border-bottom: 2px solid #2e2e2c; }

	&__img {
		max-width: 100%;
		height: auto;
		max-height: 100px;
		margin: auto; }

	&__desc {
		font-size: 22px;
		padding: 34px 0 43px;
		line-height: 1.4;
		@include tablet {
			font-size: 18px; } }


	&__btn {
		min-height: 60px;
		padding: 10px 10px;
		font-size: 14px;
		color: $themeColor;
		margin: 0 20px;
		margin: auto 20px 0;
		@include md {
			margin: auto 0 0; } } }

