.mc {
	position: relative;
	padding: 102px 0 122px;
	background-position: center top;
	background-repeat: repeat;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		bottom: -8px;
		height: 15px;
		background-image: url(../img/bg-dash.svg);
		z-index: 1; }

	@include xs {
		padding: 37px 0 55px;
 } }		//margin-top: 20px

.mc-list {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: 0 -19px;
	@include xs {
		padding-top: 20px; }
	&__item {
		position: relative;

		@include xs {
			margin: 0 auto; }
		&:not(:last-child) {
			margin-right: 45px;
			@include sm {
				margin-right: auto;
				margin-bottom: 79px; } } } }


